<script>


export default {
  data() {
    return {
      isActive: false,
      is_Active: false,
      is_Active1: false,
      checked: false,
      radioitem: 1,
      items: 0,
      is_search: false,
      searchName: '',
      langname:''
    }
  },
  mounted() {
    this.$store.dispatch('contactFunck')
    this.$store.dispatch('change')
    if(localStorage.getItem('lang')=='ru'){
      this.langname=""
    }
    if(localStorage.getItem('lang')=='uz-latn'){
      this.langname="O'zb"
    }
    if(localStorage.getItem('lang')=='uz-cyrl'){
      this.langname=""
    }
    if(localStorage.getItem('lang')=='en'){
      this.langname="Eng"
    }
  },
  methods: {
    changelang(item){
      localStorage.setItem('lang',item)
      this.$router.go()
    },
    radioFunck(item) {
      console.log(item)
      if (item == 2) {
        document.body.classList.add('changeStyle')
      }
      if (item == 1) {
        document.body.classList.remove('changeStyle')
      }
    },
    add_font_size() {
      if (this.items < 7) {
        document.body.classList.remove(`changeFonts${this.items}`)
        document.body.classList.add(`changeFonts${this.items + 1}`)
        this.items = this.items + 1;
      }

    },
    remove_font_size() {
      if (this.items > 0) {
        document.body.classList.remove(`changeFonts${this.items}`)
        document.body.classList.add(`changeFonts${this.items - 1}`)
        this.items = this.items - 1;
      }
    },
    clearClass() {
      document.body.className = ''
      this.items = 0
    },
    myFuncks() {
      this.checked = !this.checked
    },
    search() {
      this.$store.dispatch('search', this.searchName)
    }
  }
}
</script>
